export const translateIntoPortuguese = (text: string) => {
  const portugueseDictionary: { [keyword: string]: string } = {
    culture: 'cultura',
    news: 'actualidade',
    sports: 'desporto',
    science: 'cincia',
    religion: 'religio',
    fun: 'diverso',
    tech: 'tecnologia',
    weather: 'tempo',
    history: 'histria',
    en: 'ingls',
    es: 'espanhol',
    pt: 'portugus',
    all: 'todas as lnguas',
  };
  return portugueseDictionary[text];
};
