import React from 'react';
import { Paper } from '@material-ui/core';
export default function PrivacyPolicy() {
  return (
    <Paper style={{ padding: '20px 40px' }}>
      <h1>POLTICA DE PRIVACIDADE</h1>
      <p>
        Os Utilizadores do website da Portugal s Manchetes podem preencher um formulrio de pedido
        de contacto num dos websites da Portugal s Manchetes, no qual fornecem dados pessoais
        especficos.
      </p>
      <p>
        A presente Poltica de Privacidade aplica-se aos websites da Portugal s Manchetes e a
        outras interaces que possam existir entre o Utilizador e a Portugal s Manchetes (p.ex.
        perguntas sobre servios).
      </p>
      Caso no concorde com a presente poltica de privacidade, no dever aceder ou utilizar os
      servios da Portugal s Manchetes, os websites ou quaisquer outros aspetos do negcio da
      Portugal s Manchetes.
      <p></p>
      <h2>FORMULRIOS E FICHEIROS DE REGISTO</h2>
      <p>
        Nos termos do regulamento proteco de dados aplicvel na UE (RGPD), informamos que os dados
        pessoais obtidos atravs dos formulrios de registo online no sero guardados.
      </p>
      <h2>ENCARREGADO DA PROTECO DOS DADOS</h2>
      <p>
        Para comunicar com o nosso Encarregado da Proteco dos Dados, envie um e-mail para
        dpo@portugalasmanchetes.com.
      </p>
      <h2>EXACTIDO E VERACIDADE DOS DADOS ENVIADOS</h2>
      <p>
        O Utilizador garante que os dados pessoais fornecidos  Portugal s Manchetes so
        verdadeiros e exactos. O Utilizador reconhece o seu direito de acesso, retificao,
        cancelamento e oposio sobre os dados pessoais por si comunicados.
      </p>
      <h2>BASE LEGAL PARA A RECOLHA E O TRATAMENTO DOS DADOS</h2>
      <p>O consentimento expresso dado pelo Utilizador (a parte interessada).</p>
      <h2> FINALIDADE</h2>
      <p>
        A finalidade da recolha dos dados  a produo, administrao, execuo e melhoria dos
        servios para os quais o Utilizador realizou o seu registo, incluindo o prprio registo, ou
        a utilizao dos servios acima referidos por forma a ir ao encontro das preferncias do
        Utilizador, a concepo de novos servios relacionados com outros servios prestados pela
        Portugal s Manchetes, e o envio de actualizaes sobre estes mesmos servios.
      </p>
      <h2>TRANSFERNCIA DOS DADOS PARA FORA DA UNIO EUROPEIA</h2>
      <p>
        A Portugal s Manchetes no tem qualquer inteno de transferir os dados para quaisquer
        outras organizaes fora da Unio Europeia ou quaisquer outras organizaes internacionais.
      </p>
      <h2>EXERCCIO DO DIREITO AO ACESSO, RETIFICAO, ANULAO E OPOSIO AOS DADOS</h2>
      <p>
        O Utilizador poder exercer o seu direito de acesso, retificao, anulao e oposio aos
        seus dados comunicando com a Portugal s Manchetes por email, atravs do endereo de correio
        electrnico dpo@Portugal s Manchetes.com, ou por correio, por carta enviada para a Portugal
        s Manchetes. No foi definida data de expirao dos dados registados.
      </p>
      <h2>MEDIDAS DE SEGURANA</h2>
      <p>
        A Portugal s Manchetes adoptou os nveis de segurana para a proteo de dados pessoais
        exigidos por lei, tendo implementado todas as medidas e os meios necessrios para prevenir a
        perda, utilizao indevida, alterao, acesso no autorizado ou roubo dos dados pessoais. No
        entanto, o Utilizador deve estar consciente de que as medidas de segurana na Internet no
        so inexpugnveis.
      </p>
      <h2>ACEITAO E CONSENTIMENTO</h2>
      <p>
        O Utilizador declara que foi devidamente informado sobre as condies relacionadas com a
        proteo dos dados pessoais pela Portugal s Manchetes e que aceita e d o seu consentimento
         recolha e manuteno destes dados pela Portugal s Manchetes, na forma e para os fins
        indicados na presente Poltica de Proteco de Dados Pessoais.
      </p>
      <h2>RECLAMAES</h2>
      <p>
        O Utilizador (a parte interessada) poder apresentar uma reclamao junto  autoridade de
        superviso competente para fazer valer os seus direitos legais, como especificado acima.
      </p>
    </Paper>
  );
}
