import React, { useState, useEffect } from 'react';
import { IArticle } from 'models/IArticle';
import {
  Avatar,
  Grid,
  ListItem,
  ListItemIcon,
  ListItemText,
  List,
  createStyles,
  makeStyles,
  Theme,
  darken,
  useTheme,
  Badge,
  useMediaQuery,
  lighten,
} from '@material-ui/core';
import { Alert } from '@material-ui/lab';
import ArticleIcon from '@material-ui/icons/LibraryBooksTwoTone';

import Article from 'components/article/article';

import 'react-loader-spinner/dist/loader/css/react-spinner-loader.css';
import NewsLoader from 'components/loader/loader';
import { Link, useParams } from 'react-router-dom';
import InfiniteScroll from 'components/infinite-scroll/infinite-scroll';
import { IPagination } from 'models/IPagination';
import { useCategories } from 'context/settings-context';
import { getApiUrl } from './apiFunctions';
import { List as IEnumerable } from 'linq-collections';
import { IDailyTopic } from 'models/IDailyTopic';
import CategoriesList from 'components/explore/categories/categories-list';
import { translateIntoPortuguese } from 'lib/localizer';
import { getDaysSince2020First } from 'lib/utils';
import classes from '*.module.css';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    alert: {
      backgroundColor: 'transparent',
    },

    mainContainer: {
      [theme.breakpoints.up('md')]: {
        padding: '8px 16px',
      },
    },
    topic: {
      marginBottom: 32,

      [theme.breakpoints.up('md')]: {
        // backgroundColor: 'white',
        // padding: '32px 64px',
        paddingRight: 0,

        // boxShadow: '0 2px 6px rgba(0,0,0,.15)',
        marginBottom: 64,
        // borderRadius: 5,
      },
    },
    topicInfo: {
      backgroundColor: lighten(theme.palette.primary.main, 0.85),
      padding: '8px 16px',
      borderRadius: 5,
      color: theme.palette.primary.main,
    },
    tagLink: {
      color: theme.palette.primary.main,
      display: 'block',
      fontWeight: 'bold',
      width: 'auto',
      '&:hover': {
        textDecoration: 'underline',
      },
      '&:visited': { color: theme.palette.primary.main },
    },
  })
);

const populateTerms = (textTags: string) => {
  const parts = textTags.split('>');
  const mainTerm = parts.shift()?.trim();
  const secondaryTerms = parts[0]?.split('|').map((t) => t.trim());

  return {
    mainTerm,
    secondaryTerms,
  };
};
export interface IFrontContainerProps {
  direction: 'row' | 'column';
}
const pageSize = 5;
const baseUrl = `https://pokopek.com/api/dailytopics`;
export default function FrontContainer({ direction }: IFrontContainerProps) {
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'), { noSsr: true });
  //state
  const [err, setErr] = useState<Boolean>(false);
  const [pagination, setPagination] = useState<IPagination>();

  const [dailyTopics, setDailyTopics] = useState<IDailyTopic[]>([]);
  const [isLoading, setIsLoading] = useState<boolean>(true);

  //pageNumber starts at 2 because first page is loaded by useffect
  const [pageNumber, setPageNumber] = useState<number>(2);
  const { categories } = useCategories();
  let { language, daysSince2020First } = useParams<any>();
  let mainTopic: string;
  let secondaryTopics: string[] = [];
  //if language is not set
  language = language ?? localStorage.getItem('language') ?? 'all';

  // for fetch articles
  //--Could be custom hook!
  useEffect(() => {
    const fetchTopics = async () => {
      var apiUrl = getApiUrl({
        baseUrl,
        pageNumber: 1,
        pageSize,
        categories,
        daysSince2020First,
        language,
      });
      try {
        const response = await fetch(apiUrl);
        let paginationHeaders = response.headers.get('X-Pagination')!;
        let paginationJson: IPagination = JSON.parse(paginationHeaders);
        setPagination(paginationJson);
        const newTopics: IDailyTopic[] = await response.json();

        setDailyTopics(newTopics);

        if (err === true) setErr(false);
        setIsLoading(false);
      } catch {
        setErr(true);
      }
    };

    setIsLoading(true);
    fetchTopics();

    //2 (two), because the first page has been got here, and the infinite scroll should take the second page
    setPageNumber(2);
  }, [language, categories, daysSince2020First]);

  //-- LOAD MORE when reaches end of page
  const loadMore = async () => {
    if (isLoading || !pagination?.HasNext) {
      return;
    }

    var apiUrl = getApiUrl({
      baseUrl,
      pageNumber,
      pageSize,
      categories,
      daysSince2020First,
      language,
    });

    const response = await fetch(apiUrl);
    let paginationHeaders = response.headers.get('X-Pagination')!;
    let paginationJson: IPagination = JSON.parse(paginationHeaders);
    setPagination(paginationJson);
    const topics = await response.json();
    const mergedTopics = new IEnumerable<IDailyTopic>([...dailyTopics, ...topics])
      .distinct((a) => a.id)
      .toArray();

    setDailyTopics(mergedTopics);
    setPageNumber(pageNumber + 1);
  };

  //--RENDER

  return (
    <Grid container>
      {err && (
        <Alert severity="error">
          Pedimos desculpas. Neste momento, o site est a implementar algumas mudanas. Volte mais
          tarde
        </Alert>
      )}
      <Grid item md={12}>
        <h3 style={{ width: '100%', paddingLeft: 8 }}>Destaques </h3>
        {isLoading && <NewsLoader text="a carregar" fontSize={14} />}
        <InfiniteScroll
          dataLength={dailyTopics.length}
          endMessage={
            <Alert
              style={{ marginTop: 18, maxWidth: direction === 'column' ? '100' : '83%' }}
              severity="info"
            >
              No h mais destaques por hoje en <b>{translateIntoPortuguese(language)}</b> nas
              categorias selecionadas.
            </Alert>
          }
          next={loadMore}
          hasMore={isLoading || pagination === undefined || pagination?.HasNext}
          loader={null}
          style={{ overflow: 'hidden' }}
        >
          <Grid container className={classes.mainContainer}>
            <Grid item md={8}>
              {!isLoading &&
                dailyTopics.map((topic: IDailyTopic) => {
                  const articles = sortArticles(topic);
                  const mainArticle = articles.shift()!;
                  const terms = populateTerms(topic.tagsText);
                  return (
                    <Grid item container key={topic.id} className={classes.topic}>
                      <Grid item xs={12} md={10}>
                        <h3
                          style={{
                            textTransform: 'uppercase',
                            display: 'flex',
                            alignItems: 'baseline',
                            marginBottom: 8,
                          }}
                        >
                          <Link
                            className={classes.topicInfo}
                            style={{ color: theme.palette.primary.main }}
                            to={`/${language}/search/"${
                              terms.mainTerm
                            }"/${getDaysSince2020First()}`}
                          >
                            {terms.mainTerm}
                            <span
                              style={{
                                marginLeft: 18,
                                fontSize: 12,
                                color: theme.palette.text.secondary,
                              }}
                            >
                              <ArticleIcon
                                style={{
                                  fontSize: 18,
                                  top: 5,
                                  position: 'relative',
                                  marginRight: 4,
                                }}
                              />
                              {topic.count}
                            </span>{' '}
                          </Link>
                        </h3>
                      </Grid>

                      <Grid item container key={topic.id} spacing={3}>
                        <Grid item xs={12} md={10}>
                          <Article direction="row" {...mainArticle} bordered />
                        </Grid>

                        <Grid item xs={12} md={10}>
                          {ArticlesList(articles, language, terms.secondaryTerms, classes.tagLink)}
                        </Grid>
                      </Grid>
                    </Grid>
                  );
                })}
            </Grid>
            {!isMobile && (
              <Grid item md={3}>
                <CategoriesList position="fixed" />
              </Grid>
            )}
          </Grid>
        </InfiniteScroll>
      </Grid>
    </Grid>
  );
}

function ArticlesList(articles: IArticle[], language: any, terms: string[], styles: string) {
  return (
    <List>
      {articles.map((art: IArticle, i: number) => {
        return art === undefined ? (
          ''
        ) : (
          <ListItem
            key={art.id}
            button
            style={{ padding: 4 }}

            // -- TODO
            // component={(props) => <Link to={`/${language}/**TAGS**/**TODO**`} {...props} />}
          >
            <ListItemIcon>
              <Avatar src={`${art.favicon}`} style={{ width: 30, height: 30 }} />
            </ListItemIcon>
            <div style={{ display: 'flex', flexDirection: 'column' }}>
              <Link
                className={styles}
                to={`/${language}/search/${terms[i + 1]}/${getDaysSince2020First()}`}
              >
                {terms[i + 1]}
              </Link>
              <ListItemText primary={art.title}></ListItemText>
            </div>
          </ListItem>
        );
      })}
    </List>
  );
}

function sortArticles(topic: IDailyTopic): IArticle[] {
  let { secondaryTerms } = populateTerms(topic.tagsText);
  let articles = topic.articles.slice();

  let arts = secondaryTerms
    .map((term) => {
      const art = articles
        .filter((a) =>
          a.tags.some(
            (tag) =>
              tag.toLowerCase().includes(term.toLowerCase()) ||
              term.toLowerCase().includes(tag.toLowerCase())
          )
        )
        .shift()!;

      articles = articles.filter((a) => a !== art);

      return art;
    })
    .filter((a) => a != undefined);

  const result = arts.filter((a) => a != undefined);
  if (arts.length === 0) return articles;
  return arts;
}
