import React from 'react';
import LocalOfferTwoTone from '@material-ui/icons/LocalOfferTwoTone';

import { makeStyles, Theme, createStyles } from '@material-ui/core';

import { Link } from 'react-router-dom';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    alert: {
      backgroundColor: 'transparent',
    },
    tag: {
      marginLeft: 12,

      display: 'inline-flex',
      alignItems: 'center',
      flexDirection: 'row',

      fontSize: (props) => (props.size == 'big' ? '.84rem' : '.64rem'),
      color: (props: LinkTagProps) => props.color,
      borderRadius: '2em',

      padding: '0px 8px 0 3px',
      //   backgroundColor: (props) => (props.color ? lighten(props.color, 0.9) : 'transparent'),
    },
    tagIcon: { transform: 'rotate(135deg)', marginRight: 8, maxHeight: '10px !important ' },
    mainContainer: {
      [theme.breakpoints.up('md')]: {
        padding: '8px 16px',
      },
    },
    topic: {
      marginBottom: 32,

      [theme.breakpoints.up('md')]: {
        // backgroundColor: 'white',
        // padding: '32px 64px',
        paddingRight: 0,

        // boxShadow: '0 2px 6px rgba(0,0,0,.15)',
        marginBottom: 64,
        // borderRadius: 5,
      },
    },
  })
);

export interface LinkTagProps {
  to: string;
  text: string | number;
  size?: 'small' | 'big';
  icon?: any;

  color?: string;
}
export default function LinkTag({
  to,
  text,
  icon,
  size = 'big',
  color = 'rgba(0, 0, 0, 0.77)',
}: LinkTagProps) {
  const classes = useStyles({ to, color, size, text });
  return (
    <Link to={to} className={classes.tag}>
      {icon === true && <LocalOfferTwoTone style={{ fontSize: 10 }} />}
      {icon ?? icon} <span style={{ marginRight: 8 }} />
      <span>{text}</span>
    </Link>
  );
}
